<template>
    <div class="modifyPW">
          <van-nav-bar
        title="安全中心"
        left-text="返回"
        @click-left="back"
        left-arrow
        />
        <div class="box">
            <div class="box_newpasw">
                
                <div class="box_but" v-for="(item,index) in allBoxBut" :key="index" v-show="item.isHas">
                    <van-button type="primary" block round @click="modifyAndSOtAndCCellation(item,index)" :color="item.color">{{item.title}}</van-button>
                </div>
                <div class="submi" v-if="isHas">
                    <van-form @submit='submitPW'>
                        <van-field
                            v-model="userInfo.oldPassword"
                            name="旧密码"
                            placeholder="旧密码"
                            label="旧密码"
                            type="password"
                        />
                        <van-field
                            v-model="userInfo.password"
                            type="password"
                            name="新密码"
                            label="新密码"
                            placeholder="新密码"
                        />
                        <div style="margin: 16px;">
                            <van-button round block type="info" native-type="submit">
                            提交
                            </van-button>
                        </div>
                    </van-form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import validForm from '../assets/js/validForm'
    export default {
        data(){
            return {
                userInfo:{
                    oldPassword: '',
                    password: ''
                },
                allBoxBut:[
                    {title: '修改密码',color:"green",isHas: true},
                    {title: '退出登录',color:"green",isHas: true},
                    {title: '注销账号',color:"green",isHas: true}
                ],
                isHas: false
            }
        },
        methods:{
            back(){
                this.$router.go(-1);
            },
            modifyAndSOtAndCCellation(item,index){

                let AxixsData={
                    method: "POST",
                    url: '',
                    data:{
                       appkey: this.appkey,
                       tokenString
                    }
                }
                let tokenString = localStorage.getItem('__Tk');
                if(index==0){
                    this.isHas=true;
                    this.allBoxBut[index].color="red";
                    for(let i=0;i<this.allBoxBut.length;i++){
                        this.allBoxBut[i].isHas=false;
                    }
                 
                }else if(index == 1){
                    AxixsData.url='/logout';
                    this.axiosPost(AxixsData);
                   
                }else{
                   
                    this.$dialog.confirm({
                        title: '注销账号',
                        message: '是否确定注销账号，一旦注销账号无法恢复...',
                    }).then(() => {
                         AxixsData.url='/destroyAccount';
                       this.axiosPost(AxixsData);
                         
                    }).catch(err => {
                        
                    });
                }

               
            },
            axiosPost(AxixsData){
                 
                    
                    this.$toast.loading({
                        message: '加载中...',
                        forbidClick: true,
                        duration: 0
                    })

                    this.axios(AxixsData).then(result=>{
                        this.$toast.clear(); 
                        if(AxixsData.url=='/logout' ||AxixsData.url=='/destroyAccount'){
                            localStorage.removeItem('__Tk');
                            this.$router.push({name: 'Login'});
                        }   
                        
                    }).catch(err=>{
                        this.$toast.clear();
                        
                    })
            
            },
            submitPW(){
                if(!validForm.validUserForm(this.userInfo,this)){
                    return;
                }
                 let tokenString = localStorage.getItem("__Tk");

                  this.$toast.loading({
                        message: "加载中...",
                        forbidClick: true,
                        duration: 0
                    });

                    this.axios({
                        method: "POST",
                        url: "/updatePassword",
                        data: {
                        appkey: this.appkey,
                        tokenString,
                        
                        password: this.userInfo.password,
                       
                        oldPassword: this.userInfo.oldPassword
                        }
                    }).then(result => {
                        this.$toast.clear();
                        
                        if (result.data.code == 'E001') {
                            //清除token
                            localStorage.removeItem('__Tk');
                            this.$router.push({name: 'Login'});
                        } else {
                            this.$toast(result.data.msg);
                        }
                        
                    }).catch(err => {
                        this.$toast.clear();
                        
                    });
            }
        }
        
    }
</script>

<style lang="less" scoped>
      .box_newpasw{
          padding-top: 20px;
      }
      .box_but{
          padding: 20px 50px;
      }
</style>